import { Pagination } from "@mui/material";
import { Stack } from "@mui/system";
import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link, useNavigate } from "react-router-dom";
import { searchThunk } from "../../redux/categorySlice";
import { deleteCategoryAPI } from "../../service/category.service";

function ListCategory() {
    let navigate = useNavigate()
    const dispatch = useDispatch();
    let {
        categories,
        recordsFiltered,
        totalPage
    } = useSelector((store) => store.category);

    // let [categories, setCategories] = useState([])

    let [search, setsearch] = useState({
        "start": 0,
        "length": 5,
        "search": {
            "value": ""
        }
    })

    // let [totalPage, setTotalPage] = useState(0)

    useEffect(() => {
        fetchData()
        console.log("online");
        return () => {
            console.log("ofline");
        }
    }, [search])

    const fetchData = async () => {
        dispatch(searchThunk(search));
    }
    const handleSearch = (e) => {
        setsearch({
            ...search,
            "search": {
                "value": e.target.value
            }
        })
    }

    const handleNext = () => {
        setsearch({
            ...search,
            start: search.start + search.length
        })
    }
    const handlePrev = () => {
        setsearch({
            ...search,
            start: search.start - search.length
        })
    }

    const handleChangeSize = (e) => {
        const size = e.target.value
        setsearch({
            ...search,
            length: parseInt(size)
        })
    }

    const handlePage = (index) => {
        setsearch({
            ...search,
            start: index * search.length
        })
    }

    const handleDelete = async (id) => {
        try {
            await deleteCategoryAPI(id)
            fetchData()

        } catch (e) {
        }
    }

    return ( <div>
        <Link to="/category/create">Create Category</Link>
        <div>
            <input onChange={handleSearch} />
        </div>
        <table border={1}>
            <thead>
                <tr>
                    <th>ID</th>
                    <th>Name</th>
                    <th>Actions</th>
                </tr>
            </thead>

            <tbody>
                {categories.map(c => {
                    return (
                        <tr key={c.id}>
                            <td>{c.id}</td>
                            <td>{c.name}</td>
                            <td>
                                <button onClick={() => handleDelete(c.id)}>Delete</button>
                                | <button onClick={() => navigate(`/category/update/${c.id}`)}>Edit</button>
                            </td>
                        </tr>
                    )
                })}
            </tbody>
        </table>
        <Stack direction={"row"} alignItems={"center"} />
        <select onChange={handleChangeSize}>
            <option value="5">5</option>
            <option value="10">10</option>
            <option value="20">20</option>
        </select>
        <button onClick={handlePrev}>Prev</button>
        {
            Array.apply(null, { length: totalPage }).map((_, index) => {
                console.log(index);
                return (
                    <button onClick={() => handlePage(index)}>{index}</button>
                )
            })
        }
        <button onClick={handleNext}>Next</button>

        <Pagination count={totalPage} variant="outlined" color="primary" showFirstButton showLastButton />
        onChange={(e, value) => handlePage(value - 1)}
    </div>)
}
export default ListCategory