import { useState } from "react"
import { useNavigate } from "react-router"
import { addCategoryAPI } from "../../service/category.service"
import { axiosJmaster } from "../../util/axios"
import { toast } from 'react-toastify';
import ViewCategory from "./ViewCategory";

function AddCategory() {

    let [category, setCategory] = useState({name: ""})

    let navigate = useNavigate()

    let handleChangName = (e) =>{
        setCategory({...category, name: e.target.value})
    }

    let handleSubmit = async () => {
      await addCategoryAPI(category)
      toast("Them thanh cong")
      navigate("/categories")
    }
    return <div>
        <ViewCategory/>
        <div>
           
            <label>Name:</label>
            <input name="name" onChange={handleChangName} />

            <button onClick={handleSubmit}>Save</button>
        </div>
    </div>
}
export default AddCategory