import { useContext } from "react"
import { BrowserRouter, Navigate, Outlet, Route, Routes } from "react-router-dom"
import Footer from "./component/shared/Footer"
import Menu from "./component/shared/Menu"
import { AuthContext, AuthProvider } from "./context/AuthContext"
import { ThemeProvider } from "./context/ThemeContext"
import AddCategory from "./page/category/AddCategory"
import ListCategory from "./page/category/ListCategory"
import UpdateCategory from "./page/category/UpdateCategory"
import LoginPage from "./page/LoginPage"
import ListPost from "./page/post/ListPost"
import TestPage from "./page/TestPage"
import AddPost from "./page/post/AddPost"
import UpdatePost from "./page/post/UpdatePost"
import { ToastContainer, toast } from 'react-toastify';
import ListUser from "./page/user/ListUser"
import AddUser from "./page/user/AddUser"
import Header from "./component/shared/Header"
import { store } from "./redux/store"
import { Provider } from "react-redux"

function App() {
  return (
    <Provider store={store}>
    <ThemeProvider>
      
      <AuthProvider>
        <ToastContainer />
        <BrowserRouter basename="/public">

        <Routes>
            <Route path="/login" element={<LoginPage />}></Route>

            <Route path="/" element={<MainLayout/>}>
              <Route path="/" element={<Navigate to="/posts" />} index></Route>

              {/* user */}
              <Route path="/users" element={<ListUser />}></Route>
              <Route path="/user/create" element={<AddUser />}></Route>
              
              {/* post */}
              <Route path="/posts" element={<ListPost />}></Route>
              <Route path="/post/create" element={<AddPost />}></Route>
              <Route path="/post/update/:id" element={<UpdatePost />}></Route>
            
              {/* category */}
              <Route path="/categories" element={<ListCategory />}></Route>
              <Route path="/category/create" element={<AddCategory />}></Route>
              <Route path="/category/update/:id" element={<UpdateCategory />}></Route>

          </Route>

          <Route path="/*" element={<NotFound />}></Route>

        </Routes>

        </BrowserRouter>
      </AuthProvider>
    </ThemeProvider>
    </Provider>
  )
}

function NotFound() {
  return <div>404</div>
}

function MainLayout() {

  let { auth } = useContext(AuthContext)
  if (auth)
  return (<div>
    <Menu />

    {/* se dc thay the boi element trong route */}
    <Outlet />

    <Header />

    <Footer />
  </div>
  )
  return <Navigate to="/login"/>
}
export default App