import { useState } from "react"
import { useNavigate } from "react-router"
import { addCategoryAPI } from "../../service/category.service"
import { axiosJmaster } from "../../util/axios"
import { toast } from 'react-toastify';
import { addUserAPI } from "../../service/user.service";

function AddUser() {

    let [user, setUser] = useState({name: ""})

    let navigate = useNavigate()

    let handleChangName = (e) =>{
        setUser({...user, name: e.target.value})
    }

    let handleSubmit = async () => {
        let data = new FormData();
        data.append('name', user.name)
        data.append('password', user.password)
        data.append('phone', user.phone)
        data.append('enabled', user.enabled)
        data.append('deviceId', user.deviceId)
        data.append('createdDate', user.createdDate)
        data.append('roles', user.roles)
        data.append('createdBy', user.createdBy)
        data.append('createdById', user.createdById)


      await addUserAPI(user)
      toast("Them thanh cong")
      navigate("/users")
    }
    return <div>
        <div>
            <label>Name:</label>
            <input name="name" onChange={handleChangName} />
        </div>
        <div>
            <label>Password :</label>
            <input name="password" onChange={handleChangName} />
        </div>
        <div>
            <label>Phone :</label>
            <input name="phone" onChange={handleChangName} />
        </div>
        <div>
            <label>Enabled:</label>
            <input name="enabled" onChange={handleChangName} />
        </div>
        <div>
            <label>DeviceId:</label>
            <input name="deviceId " onChange={handleChangName} />
        </div>
        <div>
            <label>CreatedDate:</label>
            <input name="createdDate " onChange={handleChangName} />
        </div>
        <div>
            <label>Roles:</label>
            <input name="roles" onChange={handleChangName} />
        </div>
        <div>
            <label>CreatedBy :</label>
            <input name="createdBy" onChange={handleChangName} />
        </div>
        <div>
            <label>CreatedById :</label>
            <input name="createdById " onChange={handleChangName} />
        </div>
        <div>
            <button onClick={handleSubmit}>Save</button>
        </div>
    </div>
}
export default AddUser