import { axiosJmaster } from "../util/axios";

//chuyen toan bo
export function searchCategoryAPI(search) {  
 var data = JSON.stringify(search);
          
          var config = {
            method: 'post',
            url: '/category/search',
            headers: { 
              'Content-Type': 'application/json'
            },
            data : data
          };
          
         return axiosJmaster(config)
          .then(function (response) {
            let body = response.data //du lieu tra ve
            return body
          })
          .catch(function (error) {
            console.log(error);
          });
}
export let addCategoryAPI = (category) => {  
  var data = JSON.stringify(category);
           
      var config = {
        method: 'post',
        url: '/admin/category/add',
        headers: { 
          'Authorization': 'Bearer ' + localStorage.getItem("token"), 
          'Content-Type': 'application/json', 
        },
        data : data
      };
      
      axiosJmaster(config)
      .then(function (response) {
        console.log(JSON.stringify(response.data));
        
      })
      .catch(function (error) {
        console.log(error);
      });
 }
 export const deleteCategoryAPI = (id) => {  
      var config = {
        method: 'delete',
        url: '/admin/category/delete?id=' + id,
        headers: { 
          'Authorization': 'Bearer ' + localStorage.getItem("token"),
      }
    };
     return axiosJmaster(config)
      .then(function (response) {
        return response.data;
      })
      .catch(function (error) {
        console.log(error);
      });
 }
 export let getCategoryAPI = (id) => {  
  var config = {
    method: 'get',
    url: '/category/' + id,
    headers: { 
      'content-Type': 'aplication/json',
  }
};
 return axiosJmaster(config)
  .then(function (response) {
    return response.data;
  })
  .catch(function (error) {
    console.log(error);
  });
}
export let updateCategoryAPI = (category) => {  
  var data = JSON.stringify(category);
           
      var config = {
        method: 'put',
        url: '/admin/category/update',
        headers: { 
          'Authorization': 'Bearer ' + localStorage.getItem("token"), 
          'Content-Type': 'application/json', 
        },
        data : data
      };
      
      axiosJmaster(config)
      .then(function (response) {
        console.log(JSON.stringify(response.data));
        
      })
      .catch(function (error) {
        console.log(error);
      });
 }