import { Search } from "@mui/icons-material";
import { createSlice } from "@reduxjs/toolkit";
import { searchCategoryAPI } from "../service/category.service";


export const categorySlice = createSlice({
    name: 'category',
    initialState: {
        categories: [],
        recordsFiltered: 0,
        totalPage: 0
    },
    reducers: {
        
        setCategories: (state, action) => {
            state.categories = action.payload;
        },
        setrecordsFiltered: (state, action) => {
            state.recordsFiltered = action.payload;
        },
        setTotalPage: (state, action) => {
            state.totalPage = action.payload
        }

    }
});
export const { setCategories, setrecordsFiltered, setTotalPage, setPage} = categorySlice.actions

export default categorySlice.reducer;

export const searchThunk = (searchDTO) => {
    return  async (dispatch, getState) => {
        try {
            let body = await searchCategoryAPI(Search) //lay then tra ve

            console.log(body.recordsFiltered); //thuoc tinh data, xem postman
            console.log(body.data)

            let totalPage = Math.ceil(body.recordsFiltered / searchDTO.length)

            dispatch(setCategories(body.data));
            // dispatch(setrecordsFiltered(body.recordsFiltered));
            // dispatch(setTotalPage(totalPage));
            dispatch(setPage (
                { totalPage, recordsFiltered: body.recordsFiltered}
            ))
         } catch(e) {
            console.log(e);
          };

    }
}

