import { useContext } from "react";
import { Link } from "react-router-dom";
import { AuthContext } from "../../context/AuthContext";
import { ThemeContext } from "../../context/ThemeContext";
import { useMe } from "../../hook/useAuth";

import SvgIcon from '@mui/material/SvgIcon';
import { useDispatch, useSelector } from "react-redux";
import { incNumber, increment, incrementAsync } from "../../redux/counterSlice";

function Menu () {
    let { mode, setMode } = useContext(ThemeContext)
    let { setAuth, user } = useContext(AuthContext)

    //co the dung ham hook
    const u = useMe(); //user la tra ve tu ham

    const counter = useSelector((store) => store.counter);
    const dispatch = useDispatch();
    console.log(counter);

    let handleMode = () => {
        setMode(mode == 'Light' ? 'Dark' : 'Light')
        // dispatch(increment(10));
        // dispatch(incNumber(10));
        dispatch(incrementAsync(10));

    }
    let handleLogout = () => {
        setAuth(false)
        localStorage.removeItem("token")
    }
    function HomeIcon(props) {
        return (
          <SvgIcon {...props}>
            <path d="M10 20v-6h4v6h5v-8h3L12 3 2 12h3v8z" />
          </SvgIcon>
        );
      }


    return (
        <ul spacing={2}>
            <HomeIcon color="primary" sx={{ fontSize: 40 }}/>
            <li>
                Xin chao {u.name} - {counter.x}
            </li>
            <li>
                <Link to="/">Home</Link>
            </li>
            <li>
                <Link to="/users">User</Link>
            </li>
            <li>
                <Link to="/posts">Post</Link>
            </li>
            <li>
                <Link to="/categories">Category</Link>
            </li>
            <li>
                <Link to="/login">Login page</Link>
            </li>
            <li>
                <Link to="/test">Test page</Link>
            </li>
            <li>
                <button onClick={handleLogout}>Logout</button>
            </li>
            <li>
                <button onClick={handleMode}>{mode}</button>
            </li>
        </ul>
    )
}
export default Menu