import { useSelect } from "@mui/base"
import { useEffect, useState } from "react"
import { useSelector } from "react-redux"
import { useNavigate, useParams } from "react-router"
import { store } from "../../redux/store"
import { getCategoryAPI, updateCategoryAPI } from "../../service/category.service"
import { axiosJmaster } from "../../util/axios"

function UpdateCategory() {
    //doc id
    let {id} = useParams()
    console.log(id);

    let [category, setCategory] = useState({id, name: ""})

    let navigate = useNavigate()

    let { categories } = useSelector(store => store.category);


    let getData = async() => {
        // let body = await getCategoryAPI(id)
        const body = categories.find(
            item => item.id === parseInt(id));
        setCategory(body)
    }

    useEffect(() => {getData()}, [id])

    let handleChangName = (e) =>{
        setCategory({...category, name: e.target.value})
    }
    let handleSubmit = async () =>{
      await updateCategoryAPI(category)
      navigate("/categories")
    }
    return <div>
        <div>
           
            <label>ID:</label>
            <input value={id} readOnly />

            <label>Name:</label>
            <input name="name" value={category.name} onChange={handleChangName} />

            <button onClick={handleSubmit}>Save</button>
        </div>
    </div>
}
export default UpdateCategory