import { AccountCircle } from "@mui/icons-material";
import { Stack } from "@mui/material";
import Login from "../component/auth/Login";
import Footer from "../component/shared/Footer";


function LoginPage() {
    return (
        <Stack spacing={2}
        direction="column"
        justifyContent="center"
        alignItems="center">
        <AccountCircle />
        <Login />
        <Footer />
    </Stack>
    )
}
export default LoginPage