import { axiosJmaster } from "../util/axios";

//chuyen toan bo
export let addPostAPI = (data) => {
  var config = {
    method: 'post',
    url: '/member/post/add',
    headers: {
      'Authorization': 'Bearer ' + localStorage.getItem("token")
    },
    data: data
  };

  return axiosJmaster(config)
    .then(function (response) {
      console.log(JSON.stringify(response.data));
      return response.data
    })
    .catch(function (error) {
      console.log(error);
    });

}
