export function searchPostAPI(search) {
  var data = JSON.stringify(search);

  var config = {
    method: 'post',
    url: '/post/search',
    headers: {
      'Content-Type': 'application/json'
    },
    data: data
  };

  return axiosJmaster(config)
    .then(function (response) {
      let body = response.data //du lieu tra ve
      return body
    })
    .catch(function (error) {
      console.log(error);
    });
}
//chuyen toan bo ham goi api vao day
export let getPostAPI = (id) => {
  var config = {
    method: 'get',
    url: '/post/' + id,
    headers: {
      'Content-Type': 'application/json',
    }
  };

  return axiosJmaster(config)
    .then(function (response) {
      return response.data;
    })
    .catch(function (error) {
      console.log(error);
    });

}

export const deletePostAPI = (id) => {
  var config = {
    method: 'delete',
    url: '/admin/post/delete?id=' + id,
    headers: {
      'Authorization': 'Bearer ' + localStorage.getItem("token"),
    }
  };
  return axiosJmaster(config)
    .then(function (response) {
      return response.data;
    })
    .catch(function (error) {
      console.log(error);
    });

}



