import axios from "axios";
import nProgress from "nprogress";

export const axiosJmaster = axios.create({
    baseURL: "https://jmaster.io/jmaster-reactjs/api"
})
axiosJmaster.interceptors.request.use(function (config) {
    nProgress.start();
    return config;
}, function (error) {
    nProgress.done();
    // Do something with request error
    return Promise.reject(error);
})
axiosJmaster.interceptors.response.use(function (resp) {
    nProgress.done();
    return resp;
}, function (error) {
    nProgress.done();
    // Do something with request error
    return Promise.reject(error);
});