import { useEffect } from "react"
import { useState } from "react"
import ReactQuill from 'react-quill';
import 'react-quill/dist/quill.snow.css';
import { useNavigate } from "react-router"
import { searchCategoryAPI } from "../../service/category.service"
import { addPostAPI } from "../../service/post.service"
import Button from '@mui/material/Button';

function AddPost() {

  let [categories, setCategories] = useState([])
  let [search, setsearch] = useState({
    "start": 0,
    "length": 20,
    "search": {
      "value": ""
    }
  })

  useEffect(() => {
    fetchData()
}, [search])

const fetchData = async () => {
    try {
        let body = await searchCategoryAPI(search) //lay then tra ve

        console.log(body.recordsFiltered); //thuoc tinh data, xem postman
        setCategories(body.data)

        //tinh tong so trang
        let totalPage = Math.ceil(body.recordsFiltered / search.length)
        
     } catch(e) {
        console.log(e);
      };
  
}

    let [post, setPost] = useState({
      title: "",
      description: "",
      categoryId: "",
      imageFile: null,
    })

    let navigate = useNavigate()

    let handleChangText = (e) =>{
        setPost({...post, [e.target.name]: e.target.value})
    }

    let handleChangFile = (e) =>{
      console.log(e.target.files[0])
      setPost({...post, imageFile: e.target.file[0] })
  }
       
    let handleSubmit = async () => {
      let data = new FormData();
      data.append('title', post.title)
      data.append('description', post.description)
      data.append('categoryId', post.categoryId)
      data.append('imageFile', post.imageFile)

      await addPostAPI(data)
      navigate("/posts")
    }
    return <div>
      <h1>New Post</h1>
        <div>
           <div>
            <label>Title:</label>
            <input name="title" onChange={handleChangText} />
            </div>
            <div>
            <label>Description:</label>
              {/* <input name="description" onChange={handleChangText} /> */}

              <ReactQuill style={{ height: 200}} theme="snow" value={post.description}
              onChange={(text) => setPost({...post, description: text })} />
              </div>
            <div style={{marginTop: 50}}>
              <label>CategoryId:</label>
              <select name="categoryId" onChange={handleChangText} >
                {categories.map(c=>{
                  return <option key={c.id} value={c.id}>{c.name}</option>
                })}
              </select>
            </div>
            <div>
              <label>Image File:</label>
              <input type="file" accept="image/*" onChange={handleChangFile} />
            </div>
            <Button variant="contained" onClick={handleSubmit}>Save</Button>

        </div>
    </div>
  
}
export default AddPost