import { Pagination } from "@mui/material";
import { Stack } from "@mui/system";
import axios from "axios";
import { useContext, useEffect, useState } from "react"
import { createRoutesFromElements, Link, useNavigate } from "react-router-dom"
import { ThemeContext } from "../../context/ThemeContext";
import { deleteCategoryAPI, searchCategoryAPI } from "../../service/category.service";
import { deleteUserAPI, searchUserAPI } from "../../service/user.service";
import { axiosJmaster } from "../../util/axios";

function ListUser() {

    let navigate = useNavigate()

    let [users, setUsers] = useState([])
   
    let [search, setsearch] = useState({
        "start": 0,
        "length": 20,
        "search": {
          "value": ""
        }
      })

    let [totalPage, setTotalPage] = useState(0)

    useEffect(() => {
        fetchData()
    }, [search])

    const fetchData = async () => {
        try {
            let body = await searchUserAPI(search) //lay then tra ve

            console.log(body.recordsFiltered); //thuoc tinh data, xem postman
            setUsers(body.data)

            //tinh tong so trang
            let totalPage = Math.ceil(body.recordsFiltered / search.length)
            setTotalPage(totalPage)
         } catch(e) {
            console.log(e);
          };
          
          
    }
    const handleSearch = (e) => {
           setsearch({...search, 
            "search": {
                "value": e.target.value
            }
        })
    }

    const handleNext = () => {
        setsearch({...search, 
            start: search.start + search.length})
    }
    const handlePrev= () => {
        setsearch({...search, 
            start: search.start - search.length})
    }

    const handleChangeSize = (e) => {
        const size = e.target.value
        setsearch({
            ...search, 
            length: parseInt(size)
        })
    }

    const handlePage = (index) => {
        setsearch({...search, 
            start: index * search.length})
    }

    const handleDelete = async(id) => {
        try {
            await deleteUserAPI(id)
            fetchData()
        } catch(e) {

        }
    }

    return <div>
        <Link to="/User/create">Create User</Link>
        <div>
            <input onChange={handleSearch} />
        </div>


        <table border={1}>
            <thead>
                <tr>
                    <th>ID</th>
                    <th>Name</th>
                    <th>Password</th>
                    <th>Phone</th>
                    <th>Enabled</th>
                    <th>Deviceld</th>
                    <th>CreatedDate</th>
                    <th>Roles</th>
                    <th>CreatedBy</th>
                    <th>CreatedById</th>
                    <th>Actions</th>
                </tr>
            </thead>

            <tbody>
                {users.map(c => {
                return (
                    <tr key={c.id}>
                        <td>{c.id}</td>
                        <td>{c.name}</td>
                        <td>{c.Password}</td>
                        <td>{c.Phone}</td>
                        <td>{c.Enabled}</td>
                        <td>{c.Deviceld}</td>
                        <td>{c.CreatedDate}</td>
                        <td>{c.Roles}</td>
                        <td>{c.CreatedBy}</td>
                        <td>{c.CreatedById}</td>
                        <td>
                            <button onClick={()=> handleDelete(c.id)}>Delete</button>
                             | <button onClick={() => navigate(`/user/update/${c.id}`)}>Edit</button>
                        </td>
                    </tr>
                )       
                })}
            </tbody>
        </table>
        <Stack direction={"row"} alignItems={"center"}/>
        <select onChange={handleChangeSize}>
            <option value="5">5</option>
            <option value="10">10</option>
            <option value="20">20</option>
        </select>
        <button onClick={handlePrev}>Prev</button>

        {
            Array.apply(null, { length: totalPage}).map((_, index) => {
                console.log(index);
                return (
                    <button onClick={() => handlePage(index)}>{index}</button>
                )
            })
        }
        <button onClick={handleNext}>Next</button>

        <Pagination count={totalPage} variant="outlined" color="primary" showFirstButton showLastButton/>
            onChange={(e, value) => handlePage(value -1)}

        {/* <p>Tong so trang: {totalPage}</p> */}

    </div>
}
export default ListUser