import { useEffect, useState } from "react"
import { useNavigate, useParams } from "react-router"
import { getCategoryAPI, searchCategoryAPI, updateCategoryAPI } from "../../service/category.service"
import { getPostAPI, searchPostAPI, updatePostAPI } from "../../service/post.service"
import { axiosJmaster } from "../../util/axios"
import ReactQuill from 'react-quill';
import 'react-quill/dist/quill.snow.css';

function UpdatePost() {
    let { id } = useParams()
    let [post, setPost] = useState({
      id,
      title: "",
      description: "",
      categoryId: '',
      imageFile: null
    })
    let navigate = useNavigate()
  
    let getData = async () => {
      let body = await getPostAPI(id)
      setPost(body)
    }

    useEffect(() => {
        fetchData()
    }, [id])

    let [categories, setCategories] = useState([])
    let [search, setSearch] = useState({
      "start": 0,
      "length": 20,
      "search": {
        "value": ""
      }
    })
    useEffect(() => {
        fetchData()
      }, [search])

    const fetchData = async () => {
        try {
            let body = await searchCategoryAPI(search)
            setCategories(body.data)
        } catch (e) {
            console.log(e);
        }
    }

    let handleChangText = (e) =>{
        setPost({...post, [e.target.name]: e.target.value})
    }

    let handleChangFile = (e) =>{
      setPost({...post, imageFile: e.target.file[0]})
  }
       
    let handleSubmit = async () => {
      let data = new FormData();
      data.append('title', post.title)
      data.append('description', post.description)
      data.append('categoryId', post.categoryId)
      data.append('imageFile', post.imageFile)
      data.append('id', post.id)

      console.log(data);

      await updatePostAPI(data)
      navigate("/posts")
    }
    return <div>
      <h1>New Post</h1>
        <div>
          <div>
            <label>Title:</label>
            <input value={post.title} name="title" onChange={handleChangText} />
           </div>
           <div>
              <label>Description:</label>
              {/* <input value={post.description} name="description" onChange={handleChangText} /> */}
              <ReactQuill style={{ height: 200}} theme="snow" value={post.description}
              onChange={(text) => setPost({...post, description: text})} />
            </div>
            <div>
              <label>CategoryId:</label>
              <select value={post.categoryId} descriptionname="categoryId" onChange={handleChangText} >
                {categories.map(c=>{
                  return <option key={c.id} value={c.id}>{c.name}</option>
                })}
              </select>
            </div>
            <div>
              <label>Image File:</label>
              <input type="file" accept="image/*" onChange={handleChangFile} />
            </div>
            <button onClick={handleSubmit}>Save</button>
        </div>
    </div>
}
export default UpdatePost