
import { axiosJmaster } from "../util/axios";

export const meAPI = () => {

    var config = {
        method: 'get',
        url: '/member/me',
        headers: {
            'Authorization': 'Bearer ' + localStorage.getItem("token"),
        },
       
    };

    return axiosJmaster(config)
        .then(function (response) {
            return (response.data)
        })
}
export function searchUserAPI(search) {  
    var data = JSON.stringify(search);
             
             var config = {
               method: 'post',
               url: '/user/search',
               headers: { 
                 'Content-Type': 'application/json'
               },
               data : data
             };
             
            return axiosJmaster(config)
             .then(function (response) {
               let body = response.data //du lieu tra ve
               return body
             })
             .catch(function (error) {
               console.log(error);
             });
   }
   export let addUserAPI = (user) => {  
     var data = JSON.stringify(user);
              
         var config = {
           method: 'post',
           url: '/admin/user/add',
           headers: { 
             'Authorization': 'Bearer ' + localStorage.getItem("token"), 
             'Content-Type': 'application/json', 
           },
           data : data
         };
         
         axiosJmaster(config)
         .then(function (response) {
           console.log(JSON.stringify(response.data));
           
         })
         .catch(function (error) {
           console.log(error);
         });
    }
    export const deleteUserAPI = (id) => {  
         var config = {
           method: 'delete',
           url: '/admin/user/delete?id=' + id,
           headers: { 
             'Authorization': 'Bearer ' + localStorage.getItem("token"),
         }
       };
        return axiosJmaster(config)
         .then(function (response) {
           return response.data;
         })
         .catch(function (error) {
           console.log(error);
         });
    }
    export let getUserAPI = (id) => {  
     var config = {
       method: 'get',
       url: '/user/' + id,
       headers: { 
         'content-Type': 'aplication/json',
     }
   };
    return axiosJmaster(config)
     .then(function (response) {
       return response.data;
     })
     .catch(function (error) {
       console.log(error);
     });
   }
   export let updateUserAPI = (user) => {  
     var data = JSON.stringify(user);
              
         var config = {
           method: 'put',
           url: '/admin/user/update',
           headers: { 
             'Authorization': 'Bearer ' + localStorage.getItem("token"), 
             'Content-Type': 'application/json', 
           },
           data : data
         };
         
         axiosJmaster(config)
         .then(function (response) {
           console.log(JSON.stringify(response.data));
           
         })
         .catch(function (error) {
           console.log(error);
         });
    }
